import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, map, tap } from 'rxjs/operators';
import { Response, Headers, RequestOptions, URLSearchParams, Http } from '@angular/http';
import { Inject } from '@angular/core';

@Injectable()
export class BusinessObjectService {
  public sessionId: string;
  constructor(private http: Http, @Inject('url') private url: string) {
    this.url = url || 'http://10.24.19.140:5000';
  }

  getBusinessObjectTree(): Observable<any> {
    const url = this.url + '/api/dev/main/v1.0/business-object-trees/';
    const headers = new Headers();
    headers.append('sessionId', this.sessionId);
    const options = new RequestOptions({
      params: {},
      headers: headers
    });
    return this.http.get(url, options).pipe(
      map((res: any) => res.json())
    );
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      return of(result as T);
    };
  }
}



