import { PackageView } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { BusinessObjectsSelectComponent } from './business-objects-select/business-objects-select.component';
export class BoSelectWizardPackageView extends PackageView {
    constructor(serializedState: any, private cfr: ComponentFactoryResolver, private injector: Injector) {
        super(serializedState);
        this.element = cfr.resolveComponentFactory(BusinessObjectsSelectComponent).create(injector);
    }
}



