import { Component, OnInit, ViewChild, EventEmitter, TemplateRef, Inject, Output, Input } from '@angular/core';
import { BusinessObjectEntity } from '../../businessobjectentity';
import { TreeElement } from '../../TreeElement';
import { BusinessObjectService } from '../../business-object.service';
import { isNullOrUndefined } from 'util';
import { CacheService } from '@farris/ide-devkit/services';

@Component({
  selector: 'app-business-objects-select',
  templateUrl: './business-objects-select.component.html',
  styleUrls: ['./business-objects-select.component.css']
})
export class BusinessObjectsSelectComponent implements OnInit {
  @ViewChild('btn') btns: TemplateRef<any>;
  @Output() modalClosed = new EventEmitter<any>();
  @Output() selectedBOEvent = new EventEmitter<any>();

  files: any;
  businessObject: BusinessObjectEntity;
  treeElement: TreeElement;
  selectedBO: BusinessObjectEntity;
  public expandedKeys = [];

  constructor(private businessObjectService: BusinessObjectService, private cache: CacheService) {
    this.selectedBO = new BusinessObjectEntity();
    const sessionId = this.cache.get('sessionId');
    if (isNullOrUndefined(sessionId)) {
     alert('session信息不能为空');
    } else {
      this.businessObjectService.sessionId = sessionId;
    }

  }

  ngOnInit() {
    this.files = [];
    this.treeElement = new TreeElement();
    this.businessObjectService.getBusinessObjectTree().subscribe(data => {
      this.treeElement = data;
      this.expandedKeys.push(this.treeElement.node.code);
      this.files.push(this.mappingFormData2Tree(this.treeElement));
    }, error => {
      alert('获取业务对象树报错。');
    });
  }
  mappingFormData2Tree(formData) {
    if (!formData) {
      return;
    }
    return this.recursionElements(formData);
  }

  private recursionElements(treeElement: TreeElement) {
    const elements = treeElement.children;
    let nodeObject;
    if (treeElement.node.isDetail === '1' && !isNullOrUndefined(elements) && elements.length > 0) {
      alert('明细节点下不能有业务对象，请确认。当前业务对象编号为：' + treeElement.node.id);
      return;
    }
    if (treeElement.node.isDetail !== '1') {
      nodeObject = {
        id: treeElement.node.id,
        text: treeElement.node.code,
        isDetail: treeElement.node.isDetail,
        items: []
      };
    }else {
      nodeObject = {
        id: treeElement.node.id,
        text: treeElement.node.code,
        isDetail: treeElement.node.isDetail
      };
    }
    if (!isNullOrUndefined(elements) && elements.length > 0) {
      elements.forEach(element => {
        const nodeRefObject = this.recursionElements(element);
        nodeObject.items.push(nodeRefObject);
      });
    }
    return nodeObject;
  }

  hideModal(): void {
    this.modalClosed.emit();
  }

  public hasChildren(node: any): boolean {
    if (!isNullOrUndefined(node.items) && node.items.length > 0 ) {
      return false;
    }else {
      return true;
    }
  }

  selectBO() {
    if (this.selectedBO) {
      if (this.selectedBO.isDetail === '0') {
        alert('请选择业务对象明细节点。');
        return;
      }
      // this.eventBusProxy.post('selectedBO', this.selectedBusinessObject);
      this.selectedBOEvent.emit(this.selectedBO);
      this.hideModal();
    }
  }

  nodeSelect(event) {
    this.selectedBO.id = event.dataItem.id;
    this.selectedBO.code = event.dataItem.text;
    this.selectedBO.isDetail = event.dataItem.isDetail;
  }
}



