
import { BoSelectWizardPackageView } from './views/boselectpackageview';
import { ComponentFactoryResolver, ComponentRef, Injector, Type } from '@angular/core';
import { BusinessObjectsSelectComponent } from './views/business-objects-select/business-objects-select.component';
import { GSP, Package } from '@farris/ide-devkit';

export class BusinessObjectsPackage extends Package {
  constructor(
    public gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {


    this.packageView = new BoSelectWizardPackageView(state, this.cfr, this.injector);

    this.gsp.eventBus.on(null, null, 'Command:selectBO', this, () => this.showModal());
  }

  showModal() {
    const packageViewT = new BoSelectWizardPackageView({}, this.cfr, this.injector);
    const title = 'select business object';
    const comp = packageViewT.getElement().instance as BusinessObjectsSelectComponent;

    const modalPanel = this.gsp.workspace.addModalPanel({
      item: {
        element: packageViewT.getElement(),
        modalOptions: {
          title: title,
          width: 500,
          height: 500,
          buttons: comp.btns
        }
      }
    });
    comp.modalClosed.subscribe(() => modalPanel.destroy());
  }
}



